<template>

  <div class="form">


<b-container class="form-container"> 

  <b-card no-body class="overflow-hidden text-white bg-dark size_image" >
    <b-row no-gutters>
      <b-col md="6">
        <b-card-img :src="form.user.foto" alt="Image" class="hover-shadow"></b-card-img>
      </b-col>
      <b-col md="6">
        <b-card-body title="Regístrate">


             
    <h1>Tu personaje asignado será: {{personaje}}</h1>
    <center> <b-button variant="warning" @click="reRoll()" >reroll</b-button> </center>
    <p>Email: <b-input type="email" required v-model="form.user.userMail" /></p>
    <p>Leyenda: <b-input required v-model="form.user.leyenda" /></p>
    <p>Username: <b-input required v-model="form.user.name" /></p>
    <p>Password: <b-input required type="password" v-model="password" /></p>
     <p>Confirma password: <b-input required type="password" v-model="password" /></p>

    <b-button variant="success" @click="signUp({email:form.user.userMail,password:password,newUser:form}) ">Registrar</b-button>

        </b-card-body>
      </b-col>
    </b-row>
  </b-card>


</b-container>


  </div>


</template>

<script>
import { mapActions } from "vuex";

export default {
  name: "SignUpView",
  data() {
    return {
      personaje:"no",
      random_number: null,
      starwarsapi: "",
      password: "",
      password2: "",
      form: {
        user: {
          leyenda:"",
          userMail: "",
          registerDate: Date.now(),
          lastConection: Date.now(),
          matchCounter: 100,
          matchUsers: [],
          name: "",
          foto: null,
        },
      },
      show: true,
    };
  },
  methods: {
    randomIntFromInterval() {
      return Math.floor(Math.random() * (80 - 0 + 1) + 0);
    },
    reRoll() {
      var randomInt = Math.floor(Math.random() * (80 - 0 + 1) + 0);
      this.form.user.foto = this.starwarsapi[randomInt].image;
      this.personaje = this.starwarsapi[randomInt].name;
    },
    ...mapActions(["signUp"]),
  },
  created() {
    fetch("https://akabab.github.io/starwars-api/api/all.json")
      .then((response) => response.json())
      .then((data) => (this.starwarsapi = data))
      .then(() => (this.reRoll()));
  },
};
</script>

